.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.chatInput{
  display: flex;
}

.chatPax{
  margin: 5px;
}

.messageHist{
  height:200px;
  width:50vmin;
  overflow-y: scroll;
  margin: 0;
  text-align: left;
  word-wrap: break-all;
  font-size: calc(10px + 1vmin);
}

.messageHist::-webkit-scrollbar {
  width: 0.5em; /* Set the width of the scrollbar */
}
messageHist
.container::-webkit-scrollbar-track {
  background-color: transparent; /* Set the background color of the scrollbar track */
}

.messageHist::-webkit-scrollbar-thumb {
  background-color: transparent; /* Set the color of the scrollbar thumb */
}

.formLabel{
  margin: 0;
  text-align: left;
}

h3{
  margin: 0;
}

.invertedList{
  display: flex;
  flex-direction: column-reverse;
  list-style-type: none;
}